import { NextResponse } from 'next/server'

const contentType = "text/plain"
const cacheControl = "public, immutable, no-transform, max-age=31536000"
const buffer = Buffer.from("VXNlci1hZ2VudDogKgpBbGxvdzogLwpTaXRlbWFwOiAvc2l0ZW1hcC54bWwKCgo=", 'base64')

if (false || false) {
    const fileSizeInMB = buffer.byteLength / 1024 / 1024
    if (fileSizeInMB > 8) {
        throw new Error('File size for Open Graph image "[project]/src/app/robots.txt" exceeds 8MB. ' +
        `(Current: ${fileSizeInMB.toFixed(2)}MB)\n` +
        'Read more: https://nextjs.org/docs/app/api-reference/file-conventions/metadata/opengraph-image#image-files-jpg-png-gif'
        )
    }
}

export function GET() {
    return new NextResponse(buffer, {
        headers: {
            'Content-Type': contentType,
            'Cache-Control': cacheControl,
        },
    })
}

export const dynamic = 'force-static'
